.signatures {
    text-align: start;
    font-size: 14px;
    margin: 0px;
  }

.button {
    padding: 10px;
    margin-bottom: 10px;  
    margin-top: 10px;
    border-radius: 4px;
    outline: none;
    background: #1785e5;
    border: 2px solid #1785e5;
    color: #fff;
    font-size: 14px;
    cursor: pointer;
    transition: 0.2s;
  }

.input {
  margin-bottom: 10px;  
  margin-top: 10px;
  padding: 10px;
  border-radius: 4px;
  border: 1px solid #c5c5c5;
  transition: 0.2s;
  }
  
  